.title {
  color: #999;
  text-decoration: none !important;
  transition: 0.25s ease-in-out;
  &:hover {
    color: rgba(255, 255, 255, 0.7);
  }
}
.movie-card {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  transition: 0.25s ease-in-out;
  color: #fff;
  &:hover {
    .card-image::before {
      opacity: 1;
    }
    .card-info {
      color: rgba(255, 255, 255, 0.7);
    }
  }
  &:active {
    .card-info {
      color: #fff;
    }
  }
}

.card-image-wrapper {
  position: relative;
  text-decoration: none !important;
}

.card-image {
  position: relative;
}

.card-image img {
  width: 100%;
  height: 245px;
  object-fit: cover;
  object-position: center center;
  border-radius: 8px;
}

.card-image::before {
  opacity: 0;
  content: "\f144";
  font-family: "Font Awesome 5 Pro";
  font-style: normal;
  font-weight: 100;
  font-size: 64px;
  color: #fff;
  position: absolute;
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 2px solid #39d98a;
  border-radius: 8px;
  background-color: rgba(17, 17, 24, 0.8);
  z-index: 99;
  transition: opacity 0.25s ease-in-out;
}

.card-info {
  display: grid;
}

.card-info {
  display: -webkit-box;
  font-family: "Inter", sans-serif;
  font-weight: 800;
  text-decoration: none;
  color: #999;
  transition: 0.25s ease-in-out;
  text-decoration: none !important;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  -webkit-line-clamp: 2;
}

.slider-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
  padding: 6px 7px;
  color: #39d98a;
  border-radius: 100px;
  text-decoration: none !important;
  transition: 0.25s ease-in-out;
}

.uk-heading-bullet::before {
  border-radius: 8px;
  border-left: calc(1px + 0.1em) solid #39d98a !important;
}

.footer {
  background-color: #1c1c28;
  .brend-btn {
    border: 2px solid #39d98a;
    opacity: 0.8;
    transition: 0.25s ease-in-out;
    &:hover {
      opacity: 1;
      border-color: #32be78;
    }
  }
}

@media (max-width: 1023px) {
  .card-image::before {
    display: none;
  }
  .uk-button-success-outline {
    &:hover,
    &:active {
      background-color: transparent !important;
      color: #fff;
      border: 2px solid #39d98a !important;
    }
  }
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #30313d;
}

::-webkit-scrollbar-thumb {
  background-color: #1c1c28;
}

::-webkit-scrollbar-button {
  background-color: #1f1f24;
}

.rc-pagination {
  margin: 0;
  padding: 0;
  font-size: 14px;
}
.rc-pagination ul,
.rc-pagination ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
.rc-pagination::after {
  display: block;
  clear: both;
  height: 0;
  overflow: hidden;
  visibility: hidden;
  content: " ";
}
.rc-pagination-total-text {
  display: inline-block;
  height: 28px;
  margin-right: 8px;
  line-height: 26px;
  vertical-align: middle;
}
.rc-pagination-item {
  display: inline-block;
  min-width: 28px;
  height: 28px;
  margin-right: 8px;
  font-family: Arial;
  line-height: 26px;
  text-align: center;
  vertical-align: middle;
  list-style: none;
  background-color: transparent;
  border: 2px solid #32be78;
  border-radius: 4px;
  outline: 0;
  cursor: pointer;
  user-select: none;
  transition: 0.25s ease-in-out;
}
.rc-pagination-item a {
  display: block;
  padding: 0 6px;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.25s ease-in-out;
}
.rc-pagination-item a:hover {
  text-decoration: none;
}
.rc-pagination-item:focus,
.rc-pagination-item:hover {
  border-color: #39d98a;
}
.rc-pagination-item:focus a,
.rc-pagination-item:hover a {
  color: #fff;
}
.rc-pagination-item-active {
  font-weight: 500;
  background: #32be78;
  border-color: #32be78;
}
.rc-pagination-item-active a {
  color: rgba(255, 255, 255, 0.7);
}
.rc-pagination-item-active:focus,
.rc-pagination-item-active:hover {
  border-color: #39d98a;
}
.rc-pagination-item-active:focus a,
.rc-pagination-item-active:hover a {
  color: #fff;
}
.rc-pagination-jump-prev,
.rc-pagination-jump-next {
  outline: 0;
}
.rc-pagination-jump-prev button,
.rc-pagination-jump-next button {
  background: transparent;
  border: none;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.25s ease-in-out;
  &:hover {
    color: #fff;
  }
}
.rc-pagination-jump-prev button:after,
.rc-pagination-jump-next button:after {
  display: block;
  content: "";
}
.rc-pagination-prev,
.rc-pagination-jump-prev,
.rc-pagination-jump-next {
  margin-right: 8px;
}
.rc-pagination-prev,
.rc-pagination-next,
.rc-pagination-jump-prev,
.rc-pagination-jump-next {
  display: inline-block;
  min-width: 28px;
  height: 28px;
  color: rgba(0, 0, 0, 0.85);
  font-family: Arial;
  line-height: 28px;
  text-align: center;
  vertical-align: middle;
  list-style: none;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.3s;
}
.rc-pagination-prev,
.rc-pagination-next {
  outline: 0;
}
.rc-pagination-prev button,
.rc-pagination-next button {
  color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  user-select: none;
}

.rc-pagination-prev .rc-pagination-item-link,
.rc-pagination-next .rc-pagination-item-link {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 12px;
  text-align: center;
  background-color: transparent;
  border: none;
  outline: none;
  transition: all 0.3s;
  &:hover {
    &::after {
      color: #fff;
    }
  }
}

.rc-pagination-prev button:after {
  content: "\f053";
  font-family: "Font Awesome 5 Pro";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  transition: 0.25s ease-in-out;
}
.rc-pagination-next button:after {
  content: "\f054";
  font-family: "Font Awesome 5 Pro";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  transition: 0.25s ease-in-out;
}
.rc-pagination-disabled,
.rc-pagination-disabled:hover,
.rc-pagination-disabled:focus {
  cursor: not-allowed;
}
.rc-pagination-disabled .rc-pagination-item-link,
.rc-pagination-disabled:hover .rc-pagination-item-link,
.rc-pagination-disabled:focus .rc-pagination-item-link {
  &::after {
    color: #666;
  }
  cursor: default;
}
.rc-pagination-slash {
  margin: 0 10px 0 5px;
}
.rc-pagination-options {
  display: inline-block;
  vertical-align: middle;
}
@media all and (-ms-high-contrast: none) {
  .rc-pagination-options *::-ms-backdrop,
  .rc-pagination-options {
    vertical-align: top;
  }
}
.rc-pagination-options-size-changer.rc-select {
  display: inline-block;
  width: auto;
  margin-right: 8px;
}
.rc-pagination-options-quick-jumper {
  display: inline-block;
  height: 28px;
  line-height: 28px;
  vertical-align: top;
}
.rc-pagination-options-quick-jumper input {
  width: 50px;
  margin: 0 8px;
}
.rc-pagination-simple .rc-pagination-prev,
.rc-pagination-simple .rc-pagination-next {
  height: 24px;
  line-height: 24px;
  vertical-align: top;
}
.rc-pagination-simple .rc-pagination-prev .rc-pagination-item-link,
.rc-pagination-simple .rc-pagination-next .rc-pagination-item-link {
  height: 24px;
  background-color: transparent;
  border: 0;
}
.rc-pagination-simple .rc-pagination-prev .rc-pagination-item-link::after,
.rc-pagination-simple .rc-pagination-next .rc-pagination-item-link::after {
  height: 24px;
  line-height: 24px;
}
.rc-pagination-simple .rc-pagination-simple-pager {
  display: inline-block;
  height: 24px;
  margin-right: 8px;
}
.rc-pagination-simple .rc-pagination-simple-pager input {
  box-sizing: border-box;
  height: 100%;
  margin-right: 8px;
  padding: 0 6px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  outline: none;
  transition: border-color 0.3s;
}
.rc-pagination-simple .rc-pagination-simple-pager input:hover {
  border-color: #1890ff;
}
.rc-pagination.rc-pagination-disabled {
  cursor: not-allowed;
}
.rc-pagination.rc-pagination-disabled .rc-pagination-item {
  background: #f5f5f5;
  border-color: #d9d9d9;
  cursor: not-allowed;
}
.rc-pagination.rc-pagination-disabled .rc-pagination-item a {
  color: rgba(0, 0, 0, 0.25);
  background: transparent;
  border: none;
  cursor: not-allowed;
}
.rc-pagination.rc-pagination-disabled .rc-pagination-item-active {
  background: #dbdbdb;
  border-color: transparent;
}
.rc-pagination.rc-pagination-disabled .rc-pagination-item-active a {
  color: #fff;
}
.rc-pagination.rc-pagination-disabled .rc-pagination-item-link {
  color: rgba(0, 0, 0, 0.25);
  background: #f5f5f5;
  border-color: #d9d9d9;
  cursor: not-allowed;
}
.rc-pagination.rc-pagination-disabled .rc-pagination-item-link-icon {
  opacity: 0;
}
.rc-pagination.rc-pagination-disabled .rc-pagination-item-ellipsis {
