}

.main-loading {
  position: fixed;
  display: grid;
  place-items: center;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: #1c1c28;
  z-index: 9999;

  & #logo {
    width: 200px;
    opacity: 0.5;
    animation: light 5s infinite;
    filter: grayscale(100);
  }
}

@keyframes light {
  0% {
    opacity: 0.5;
    filter: grayscale(100);
  }
  50% {
    opacity: 1;
    filter: grayscale(0);
  }
  100% {
    opacity: 0.5;
    filter: grayscale(100);
  }
}

.page-loading {
  position: fixed;
  display: grid;
  place-items: center;
  width: 100%;
  height: calc(100vh - 80px);
  top: 80px;
  left: 0;
  background-color: #1c1c28;
  z-index: 999;
}

.loading-enter {
  opacity: 0;
}

.loading-enter-active {
  opacity: 1;
  transition: opacity 300ms;
}

.loading-exit {
  opacity: 1;
}

.loading-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}

.movie-info {
  background-color: #1c1c28;
}

.serial-items {
  position: relative;
  cursor: pointer;
  & > div {
    border: 2px solid transparent;
    transition: 0.25s ease-in-out;
  }

  &.played > div {
    border-radius: 5px;
    border: 2px solid #39d98a;
  }

  &:hover {
    &::before {
      opacity: 1;
    }
  }
  &::before {
    opacity: 0;
    content: "\f144";
    font-family: "Font Awesome 5 Pro";
    font-style: normal;
    font-weight: 100;
    font-size: 64px;
    color: #fff;
    position: absolute;
    display: grid;
    place-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: 2px solid #39d98a;
    border-radius: 5px;
    background-color: rgba(17, 17, 24, 0.8);
    z-index: 99;
    transition: opacity 0.25s ease-in-out;
  }
}

.serial-items-block {
  background-color: #1c1c28;
  overflow-y: auto;
  & > div:last-child {
    margin-bottom: 1rem;
  }
  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
}

.toggle-serial-items {
  position: relative;
  cursor: pointer;
  & > div {
    border: 2px solid transparent;
    transition: 0.25s ease-in-out;
  }

  &.played > div {
    border-radius: 5px;
    border: 2px solid #39d98a;
  }
}

.toggle-serial-items-block {
  max-height: 320px;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
}

